body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.grid-container{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin-bottom:1em; 
}
.grid-container img{
   width:250px;
   height: 250px;
   margin: 1em;
   border:1px solid #ccc;
   background: url('/images/Preloader_2.gif') center no-repeat #fff;
}
.grid-container button{
   width: 200px;
   margin: 1em;
   font-size: 1.5em;
}
img{
   width: 300px;
   height: 300px;
}
.game-area{
   /* display: grid; */
   display: -ms-flexbox;
   display: flex;
   -ms-flex-wrap: wrap;
       flex-wrap: wrap;
   margin: 0 auto;
}
.cell{
   width: 100px;
   height: 100px;
}
.cell.selected{
 -webkit-box-shadow:  0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
         box-shadow:  0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
 border:3px solid white;
}
.bold-large-text{
   font-weight: 600;
   font-size: 1.5em;
}
.game-header{
   display: -ms-flexbox;
   display: flex;
   -ms-flex-pack: justify;
       justify-content: space-between;
   margin: 1em 0;
   -ms-flex-align: center;
       align-items: center;
}
.game-header > div{
   -ms-flex: 1 1;
       flex: 1 1;
}
.image-picker{
   border : 2px solid #eee;
   cursor: pointer;
   width:250px;
   height: 250px;
   margin: 1em;
   border:1px solid #ccc;   
}
.image-picker .material-icons{
  font-size: 6em;
}
button .material-icons{
   padding: 0 10px;
}


